	import chat from '@/components/chat';
	import find from '@/components/find';
	import mycenter from '@/components/mycenter';
	import tongxunlu from '@/components/tongxunlu';
 	import uniBadge from "@/components/uni-badge/uni-badge.vue"
	import musicIcon from '@/components/music-icon/myesic-icon.vue'
	import { mapState } from 'vuex';
	export default {
		data() {
			return {
				active:0,
				tabArr:[
					{
						iconClass:'myiconliaotian',
						name:'首页'
					},{
						iconClass:'myicontongxunlu',
						name:'通讯录'
					},{
						iconClass:'myiconfaxian',
						name:'发现'
					},{
						iconClass:'myiconwo',
						name:'我'
					}
				],
				showMusic:false
			}
		},
		onLoad() {
			if(this.$store.state.cur_music_msg != null){
				this.showMusic = true
			}
		},
		computed:{
			...mapState({
				cur_music_msg:'cur_music_msg',
				isPlay:'isPlay'
			}),
		},
		methods: {
			toLogin() {
				console.log("登陆");
				uni.navigateTo({

					url: '/pages/login/login'
				})
			}
		},
		components: {
			uniBadge,
			chat,
			find,
			tongxunlu,
			mycenter,
			musicIcon
		},
		onPullDownRefresh() {
			setTimeout(()=>{
				console.log("下拉刷新完成")
				uni.stopPullDownRefresh();//得到数据后停止下拉刷新
			},1500)
		},
		onReachBottom(){
			console.log("到底了")
			setTimeout(()=>{
				for(let i = 0;i<10;i++){
					this.badgeArr.push(this.badgeArr.length+1)
				}
			},1000)
			
		},
		methods:{
			changeTab(event){
				this.active = event.detail.current 
				let title = ''
				if(this.active == 0){
					title = '首页'
				}else if(this.active == 1){
					title = '通讯录'
				}else if(this.active == 2){
					title = '发现'
				}else if(this.active == 3){
					title = '个人中心'
				}
				
				uni.setNavigationBarTitle({
				    title
				});
			},
			clickTab(index){
				this.active = index;
				let title = ''
				if(this.active == 0){
					title = '首页'
				}else if(this.active == 1){
					title = '通讯录'
				}else if(this.active == 2){
					title = '发现'
				}else if(this.active == 3){
					title = '个人中心'
				}
				
				uni.setNavigationBarTitle({
				    title
				});
			},
			showPlaying(){
				uni.navigateTo({
					 url: '/pages/playing/playing'
				})
			}
		},
		components:{
			chat,
			tongxunlu,
			mycenter,
			find
		},
		watch:{
			
		}
	}
